div.post {
    max-width: 600px;
    margin-top: 0px;
    font-family: 'Roboto', sans-serif;
    background-color: white;
    padding: 50px;
}

div.Ingredients {
    display: flex;
    justify-content: space-between;
}

ul.macros,
ul.Testimonies {
    list-style-type: none;
    padding: 0;
}

ul.faq {
    text-align: left;
    padding: 0;
}

ol.directions {
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

h1.HERO {
    font-size: 50px;
    text-decoration: underline;
}